.Works {
    padding: 50px 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 90vh;
}

.worksTabs-container {
    width: 300px;
    height: 50px;
    margin: -30px 0 30px 0;
    /* background-color: rgb(255, 150, 14); */
    display: flex;
    justify-content: center;
    align-items: center;
}

.worksTabs-container nav {
    display: inherit;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

}

.worksTabs-container ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
    padding-inline-start: 0;
    position: relative;
    height: 100%;
    width: 100%;
}



.worksTabs-container ul li {
    list-style: none;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    height: 90%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.underline {
    width: 150px;
    height: 53px;
    border-radius: var(--borderRadius);
    position: absolute;
    bottom: -4px;
    background-color: #373636;
    z-index: -1;
}


.worksItems-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 50px 160px
}